<template>
  <div>
    <table class="dvs-table dvs-mb-8">
      <tr>
        <th
          v-for="(column, key) in tableColumns"
          :key="key"
          :class="{'dvs-hidden md:dvs-table-cell': column.hideMobile}"
          @click="showControls(column.key)"
        >
          <div
            class="dvs-flex"
            v-if="!column.toggleColumns"
          >
            <div>{{ column.label }}</div>
            <column-controls
              v-model="filters"
              :ref="column.key"
              :column="column"
            ></column-controls>
          </div>
          <div
            class="flex"
            v-else
          >
            <toggle-columns v-model="columns"></toggle-columns>
            {{ column.label }}
          </div>
        </th>
      </tr>
      <tr
        v-for="(record, rkey) in theRecords"
        :key="rkey"
      >
        <template v-for="(column, index) in tableColumns">
          <td
            :key="index"
            :class="{'dvs-hidden lg:dvs-table-cell': column.hideMobile}"
          >
            <cell
              v-if="column.template"
              :record="record"
              :contents="getRecordColumn(record, column.key)"
            ></cell>
            <span v-else>{{ getRecordColumn(record, column.key) }}</span>
          </td>
        </template>
      </tr>
      <tr v-if="!theRecords.length">
        <td
          class="dvs-text-center"
          :colspan="columns.length"
        >No Results Found</td>
      </tr>
    </table>

    <pagination
      class="dvs-mb-8"
      v-if="records.data && records.data.length"
      :meta="records"
      @changePage="changePage"
    ></pagination>

    <fieldset
      class="dvs-fieldset dvs-mb-4"
      v-if="!filters.single"
    >
      <label>Do you want the data paginated?</label>
      <toggle
        @change="requestRefreshRecords"
        v-model="filters.paginated"
        :id="randomString(8)"
      ></toggle>
    </fieldset>

    <fieldset
      class="dvs-fieldset dvs-mb-4"
      v-if="!filters.single && filters.paginated"
    >
      <label>What is the number of records per page?</label>
      <input
        @keyup="requestRefreshRecords"
        type="number"
        v-model="filters.limit"
      >
    </fieldset>

    <fieldset class="dvs-fieldset dvs-mb-4">
      <label>Do you only want the first record?</label>
      <toggle
        @change="requestRefreshRecords"
        v-model="filters.single"
        :id="randomString(8)"
      ></toggle>
    </fieldset>

    <fieldset class="dvs-fieldset dvs-mb-8">
      <label class="dvs-mb-8">Scopes</label>
      <ul
        class="dvs-list-reset dvs-mb-4"
        v-if="filters.scopes !== {}"
      >
        <li
          class="dvs-mb-2 dvs-px-4 py-3 dvs-flex dvs-items-center dvs-justify-between"
          v-for="(scope, key) in filters.scopes"
          :key="key"
          :style="theme.actionButtonGhost"
        >
          {{ key }}
          <div
            class="dvs-cursor-pointer"
            @click="removeScope(key)"
          >
            <close-icon
              class="dvs-pl-2 dvs-cursor-pointer"
              w="20"
              h="20"
            />
          </div>
        </li>
      </ul>

      <div class="dvs-flex">
        <input
          class="dvs-mb-4 dvs-mr-4"
          v-model="newScope"
          placeholder="New Scope Name"
          type="text"
        >
        <input
          class="dvs-mb-4"
          v-model="newScopeProperties"
          placeholder="New Scope Properties"
          type="text"
        >
      </div>
      <button
        class="dvs-btn dvs-btn-xs"
        :style="theme.actionButtonGhost"
        @click="addScope"
      >Add Scope</button>
    </fieldset>

    <fieldset class="dvs-fieldset dvs-mb-4">
      <label>Do you want to cache this data?</label>
      <toggle
        v-model="filters.cache"
        @change="requestRefreshRecords"
      ></toggle>
    </fieldset>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
import commonUtils from '../../../vuex/utils/common';

import Cell from './Cell.vue';
import ColumnControls from './ColumnControls.vue';
import Pagination from './Pagination.vue';
import Strings from '../../../mixins/Strings';
import Toggle from '../Toggle.vue';
import ToggleColumns from './ToggleColumns.vue';

export default {
  name: 'SuperTable',
  data () {
    return {
      theOptions: {
        showLinks: true,
      },
      filters: {
        related: {},
        search: {},
        sort: {},
        dates: {},
        paginated: true,
        page: '1',
        limit: 5,
        single: false,
        scopes: {},
        cache: false
      },
      refreshRecords: null,
      records: [],
      meta: {},
      newScope: '',
      newScopeProperties: '',
    };
  },
  mounted () {
    // Merge options
    this.theOptions.showLinks = this.showLinks;

    if (typeof this.editData !== 'undefined') {
      for (const scope in this.editData.filters.scopes) {
        if (this.editData.filters.scopes.hasOwnProperty(scope)) {
          const s = this.editData.filters.scopes[scope];
          for (const scopeProp in s) {
            if (s.hasOwnProperty(scopeProp)) {
              this.filters.scopes[scopeProp] = s[scopeProp];
            }
          }
        }
      }

      this.$set(this.filters, 'limit', this.editData.filters.limit);
      this.$set(this.filters, 'single', this.editData.filters.single);
      this.$set(this.filters, 'page', this.editData.filters.page);
      this.$set(this.filters, 'paginated', this.editData.filters.paginated);
      this.$set(this.filters, 'cache', this.editData.filters.cache);
    }

    this.requestRefreshRecords();
  },
  methods: {
    ...mapActions('devise', ['getModelRecords']),
    updateValue () {
      const modelQuery = `${this.model.class}&${commonUtils.buildFilterParams(this.filters)}`;
      this.$emit('input', modelQuery);
      this.$emit('change', modelQuery);
      this.$nextTick(() => {
        this.$emit('done', modelQuery);
      });
    },
    cancel () {
      this.$emit('cancel');
    },
    requestRefreshRecords () {
      const self = this;

      this.updateValue();

      clearTimeout(this.refreshRecords);

      this.refreshRecords = setTimeout(() => {
        self
          .getModelRecords({
            model: self.value,
            filters: self.filters,
          })
          .then(response => {
            self.records = response.data;
          });
      }, 500);
    },
    changePage (page) {
      this.filters.page = page;
      this.requestRefreshRecords();
    },
    showControls (key) {
      if (this.$refs.hasOwnProperty(key) && this.$refs[key][0].show === false) {
        this.$refs[key][0].show = true;
      }
    },
    // Checks to see if the key includes a period meaning that its a property of a property
    getRecordColumn (record, key) {
      if (!key.includes('.')) {
        return record[key];
      }
      return this.getRecordColumnTraversal(record, key);
    },
    // Get in there and get the property at the bottom of the stack
    getRecordColumnTraversal (record, key) {
      const parts = key.split('.[].');

      for (let i = 0; i < parts.length; i + 1) {
        const part = parts[i];

        // If it's the array part
        if (i % 2 === 1) {
          record = this.getFormattedStringFromArray(record, part);
        } else {
          record = [record].concat(part.split('.')).reduce((a, b) => a[b]);
        }
      }

      return record;
    },
    getFormattedStringFromArray (record, part) {
      let finalString = '';

      for (let i = 0; i < record.length; i + 1) {
        if (i > 0) {
          finalString += ', ';
        }

        finalString += record[i][part];
      }

      return finalString;
    },
    showColumn (column) {
      return column.show === true || typeof column.show === 'undefined';
    },
    addScope () {
      if (this.newScope !== '') {
        this.filters.scopes[this.newScope] = this.newScopeProperties;
        this.newScope = '';
        this.newScopeProperties = '';
        this.requestRefreshRecords();
      }
    },
    removeScope (key) {
      this.$delete(this.filters.scopes, key);
      this.requestRefreshRecords();
      this.updateValue();
    },
  },
  computed: {
    theRecords () {
      if (typeof this.records.data !== 'undefined') {
        return this.records.data;
      }
      if (!this.filters.single) {
        return this.records;
      }
      return [this.records];
    },
    tableColumns () {
      let columns = [];

      columns = this.columns.filter(c => {
        if (this.showColumn(c)) {
          return true;
        }
        return false;
      });

      return columns;
    },
  },
  watch: {
    model () {
      this.requestRefreshRecords();
    },
    filters () {
      this.requestRefreshRecords();
    },
  },
  props: {
    value: {
      type: String,
    },
    model: {
      type: Object,
    },
    columns: {
      type: Array,
      required: true,
    },
    showLinks: {
      type: Boolean,
    },
    editData: {
      type: Object,
    },
  },
  mixins: [Strings],
  components: {
    CloseIcon: () =>
      import(/* webpackChunkName: "devise-icons" */ 'vue-feather-icons/icons/XIcon'),
    ColumnControls,
    ToggleColumns,
    Pagination,
    Cell,
    Toggle,
  },
};
</script>
